import { Check, ChevronUp, CircleAlert, Trash2Icon, X } from "lucide-react";
import PriceFormatter from "../format-price/PriceFormatter";
import { CopyIcon, HeartIcon } from "@/assets/icons";
import { copyToClipboard } from "@/utils";
import Image from "next/image";
import { Separator } from "../ui/separator";
import { useState } from "react";
import { StoreItem } from "@/context/store";
import { ConfirmModal } from "../modal";
interface Props {
  city: string;
  setCity: (city: string) => void;
  isAllChecked: boolean;
  toggleAllItems: () => void;
  cart: StoreItem[];
  setQuantity: (id: number, quantity: number) => void;
  toggleSingleItem: (id: number) => void;
  selectedItems: number[];
}
const MyCartLeft = ({
  city,
  setCity,
  isAllChecked,
  toggleAllItems,
  setQuantity,
  cart,
  toggleSingleItem,
  selectedItems,
}: Props) => {
  const {
    isOpen: isConfirmOpen,
    message,
    openModal,
    closeModal,
    onConfirm,
  } = useConfirmModal();
  const handleDeleteClick = (id: string) => {
    openModal("Are you sure you want to delete this user?", () => {
      deleteCondition({ id });
    });
  };
  return (
    <div className="col-span-3">
      <div className="space-y-6">
        <div className="bg-white border shadow-sectionShadow py-[23px] px-[15px]">
          <div className="flex items-center gap-2">
            <h5 className="text-textColor text-base">
              –í—ã–±–µ—Ä–∏—Ç–µ –≥–æ—Ä–æ–¥ –¥–æ—Å—Ç–∞–≤–∫–∏
            </h5>
            {city === "–¢–∞—à–∫–µ–Ω—Ç" ? (
              <div className="w-5 h-5 bg-green-600 rounded-full flex items-center justify-center">
                <Check className="w-3 h-3 text-white" />
              </div>
            ) : (
              <div className="flex items-center gap-2">
                <span className="">
                  <CircleAlert className="w-4 h-4 text-orangeSun" />
                </span>
                <p className="text-xs">
                  –£—Ç–æ—á–Ω–∏—Ç–µ –≤–∞—à –≥–æ—Ä–æ–¥, —ç—Ç–æ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ –¥–ª—è –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã—Ö —Ä–∞—Å—á—ë—Ç–æ–≤
                  —Å–ø–æ—Å–æ–±–æ–≤ –¥–æ—Å—Ç–∞–≤–∫–∏ –∏–ª–∏ —Å–∞–º–æ–≤—ã–≤–æ–∑–∞.
                </p>
              </div>
            )}
          </div>
          <div className="mt-4 mb-2">
            <input
              type="text"
              className="inputs py-2.5"
              placeholder="–ù–∞—á–Ω–∏—Ç–µ –≤–≤–æ–¥–∏—Ç—å –Ω–∞–∑–≤–∞–Ω–∏–µ –≥–æ—Ä–æ–¥–∞"
              value={city || ""}
              onChange={(e) => setCity(e.target.value)}
            />
          </div>
          <button
            className={`px-[15px] h-[30px] flex items-center justify-center rounded-full  text-xs ${!city ? "bg-superSilver text-textColor " : "bg-cerulean text-white"}`}
            onClick={() => setCity("–¢–∞—à–∫–µ–Ω—Ç")}
          >
            –¢–∞—à–∫–µ–Ω—Ç
            {city && (
              <span
                className="ml-1"
                onClick={(e) => {
                  e.stopPropagation();
                  setCity("");
                }}
              >
                <X className="w-4 h-4" />
              </span>
            )}
          </button>
        </div>
        <div className="bg-white border shadow-sectionShadow py-[23px] px-[15px] text-textColor">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2">
              <input
                id="all-check"
                type="checkbox"
                checked={isAllChecked}
                onChange={toggleAllItems}
                className="bg-green-600 cursor-pointer w-[18px] h-[18px] checked:bg-green-600"
              />
              <label htmlFor="all-check" className="cursor-pointer">
                –≤—ã–±—Ä–∞—Ç—å –≤—Å–µ
              </label>
            </div>
            <div>
              <span className="text-sm font-normal text-textColor cursor-pointer">
                –û—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É
              </span>
            </div>
          </div>
        </div>
        {/* cart map */}
        {cart?.map((product) => (
          <div
            key={product.id}
            className="p-[15px] bg-white border shadow-sectionShadow text-textColor"
          >
            {/* header */}
            <div className="flex justify-between ">
              <input
                type="checkbox"
                checked={selectedItems.includes(product.id)}
                onChange={() => toggleSingleItem(product.id)}
              />
              <div className="flex h-[26px] items-center">
                <button className="flex items-center gap-2 group ">
                  <span className="text-xs group-hover:opacity-70 duration-200 ease-in-out">
                    –ø–µ—Ä–µ–Ω–µ—Å—Ç–∏ –≤ –∏–∑–±—Ä–∞–Ω–Ω–æ–µ
                  </span>
                  <span>
                    <HeartIcon className="w-[18px] h-[18px] group-hover:opacity-70 duration-200 ease-in-out" />
                  </span>
                </button>
                <div className="w-[2px] h-full  bg-superSilver mx-[15px]"></div>
                <button
                  className="flex items-center gap-2 group "
                  onClick={() => setIsOpen(true)}
                >
                  <span className="text-xs group-hover:opacity-70 duration-200 ease-in-out">
                    —É–¥–∞–ª–∏—Ç—å –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã
                  </span>
                  <span>
                    <Trash2Icon className="w-[18px] h-[18px] group-hover:opacity-70 duration-200 ease-in-out" />
                  </span>
                </button>
              </div>
            </div>
            <Separator className="my-2" />
            <div className="flex gap-2">
              {/* image */}
              <div className="border w-[130px] h-[130px] border-superSilver">
                <Image
                  src={product.image}
                  alt={product.title}
                  width={100}
                  height={100}
                  className="p-2 w-full h-full"
                />
              </div>
              <div className="w-full">
                <div className="flex flex-col gap-2">
                  <div className="flex h-fit gap-[15px]">
                    <p className="text-xs text-wasabiColor">
                      {product.article}
                    </p>
                    <span
                      className="cursor-pointer text-explosiveGrey hover:text-cerulean hoverEffect"
                      onClick={() =>
                        copyToClipboard(
                          product.article,
                          `–ê—Ä—Ç–∏–∫—É–ª ${product.article} —Å–∫–æ–ø–∏—Ä–æ–≤–∞–Ω –≤ –±—É—Ñ–µ—Ä –æ–±–º–µ–Ω–∞`
                        )
                      }
                    >
                      <CopyIcon />
                    </span>
                  </div>
                  <div className="flex h-fit gap-10 items-start justify-between w-full">
                    <p className="text-base text-textColor">{product.title}</p>
                    <span
                      className="cursor-pointer text-explosiveGrey hover:text-cerulean hoverEffect"
                      onClick={() =>
                        copyToClipboard(
                          product.title,
                          "–ù–∞–∏–º–µ–Ω–æ–≤–∞–Ω–∏–µ —Å–∫–æ–ø–∏—Ä–æ–≤–∞–Ω–æ –≤ –±—É—Ñ–µ—Ä –æ–±–º–µ–Ω–∞"
                        )
                      }
                    >
                      <CopyIcon />
                    </span>
                  </div>
                </div>
                <div className="mt-[15px]">
                  <div className="flex flex-col gap-2">
                    <div className="flex justify-between">
                      <div className="w-[90px] h-[42px] relative ">
                        <input
                          type="number"
                          value={product.quantity}
                          className="w-[90px] h-[42px] text-center pr-3 border focus:outline-none text-textColor"
                          onChange={(e) =>
                            setQuantity(product.id, Number(e.target.value))
                          }
                        />
                        <div className="absolute top-0 h-full right-[10px] flex flex-col justify-center">
                          <span
                            className="cursor-pointer"
                            onClick={() =>
                              setQuantity(
                                product.id,
                                Number(product.quantity) + 1
                              )
                            }
                          >
                            <ChevronUp strokeWidth={2.75} size={16} />
                          </span>
                          <span
                            className="cursor-pointer"
                            onClick={() =>
                              setQuantity(
                                product.id,
                                Number(product.quantity) - 1
                              )
                            }
                          >
                            <ChevronUp
                              size={16}
                              strokeWidth={2.75}
                              className="rotate-180"
                            />
                          </span>
                        </div>
                      </div>
                      <div>
                        <PriceFormatter
                          amount={product.price}
                          className="text-2xl text-textColor font-normal"
                        />
                      </div>
                    </div>
                    <div>
                      {product.inStock === "0" ? (
                        <span>–ü–æ–¥ –∑–∞–∫–∞–∑</span>
                      ) : (
                        <p>{product.inStock} —à—Ç –≤ –Ω–∞–ª–∏—á–∏–∏</p>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
      <ConfirmModal isOpen={isOpen} setIsOpen={setIsOpen} />
    </div>
  );
};

export default MyCartLeft;
