import { showError, showSuccess } from "@/components/toast/Toast";

export const copyToClipboard = (text: string, successMessage?: string) => {
  navigator.clipboard
    .writeText(text)
    .then(() => {
     showSuccess(successMessage || "–¢–µ–∫—Å—Ç —Å–∫–æ–ø–∏—Ä–æ–≤–∞–Ω –≤ –±—É—Ñ–µ—Ä –æ–±–º–µ–Ω–∞");
    })
    .catch(() => {
      showError("–û—à–∏–±–∫–∞ –ø—Ä–∏ –∫–æ–ø–∏—Ä–æ–≤–∞–Ω–∏–∏ –≤ –±—É—Ñ–µ—Ä –æ–±–º–µ–Ω–∞");
    });
};
